body {
  margin: 0;
}

.modal-overlay {
  background: #00000080;
  width: 100%;
  height: 100%;
  display: block;
  position: fixed;
  top: 0;
  left: 0;
}

.modal-content {
  background: #fff;
  border-radius: 8px;
  width: 300px;
  margin: 100px auto;
  padding: 20px;
  position: relative;
}

.close-btn {
  color: #fff;
  cursor: pointer;
  background: red;
  border: none;
  padding: 10px;
}

.form-container {
  background: #fff;
  border-radius: 10px;
  width: 300px;
  margin: 50px auto;
  padding: 20px;
  box-shadow: 0 4px 8px #0003;
}

.form-container h2 {
  text-align: center;
  margin-bottom: 20px;
}

.form-container input[type="text"] {
  box-sizing: border-box;
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 100%;
  margin-bottom: 15px;
  padding: 10px;
  font-size: 14px;
}

.submit-btn {
  color: #fff;
  cursor: pointer;
  background: #3498db;
  border: none;
  border-radius: 5px;
  width: 100%;
  padding: 10px;
  font-size: 14px;
}

.submit-btn:hover {
  background: #2980b9;
}
